const state = () => ({
  isShowRuneFilter: true,
  selectedRuneIdList: [], // 선택한 runeId 목록
  isShowItemCategoryFilter: true,
  selectedItemCategoryNameList: [], // 선택한 itemCategory 목록
  selectedSocketCountList: [],
  searchKeyword: "",
});

const getters = {
  isSelectedRuneId: (state) => (runeId) => {
    return state.selectedRuneIdList.indexOf(runeId) >= 0;
  },
  isSelectedItemCategoryName: (state) => (itemCategoryName) => {
    return state.selectedItemCategoryNameList.indexOf(itemCategoryName) >= 0;
  },
};

const mutations = {
  toggleIsShowRuneFilter(state) {
    state.isShowRuneFilter = !state.isShowRuneFilter;
  },
  // selectedRuneIdList에 runeId를 추가하거나 삭제
  toggleSelectedRuneId(state, runeId) {
    if (this.getters["diablo2/runewordSearch/isSelectedRuneId"](runeId)) {
      state.selectedRuneIdList.splice(
        state.selectedRuneIdList.indexOf(runeId),
        1
      );
    } else {
      state.selectedRuneIdList.push(runeId);
    }
  },
  clearSelectedRuneIdList(state) {
    state.selectedRuneIdList.splice(0, state.selectedRuneIdList.length);
  },
  toggleIsShowItemCategoryFilter(state) {
    state.isShowItemCategoryFilter = !state.isShowItemCategoryFilter;
  },
  toggleSelectedItemCategoryName(state, itemCategoryName) {
    if (
      this.getters["diablo2/runewordSearch/isSelectedItemCategoryName"](
        itemCategoryName
      )
    ) {
      state.selectedItemCategoryNameList.splice(
        state.selectedItemCategoryNameList.indexOf(itemCategoryName),
        1
      );
    } else {
      state.selectedItemCategoryNameList.push(itemCategoryName);
    }
  },
  clearSelectedItemCategoryNameList(state) {
    state.selectedItemCategoryNameList.splice(
      0,
      state.selectedItemCategoryNameList.length
    );
  },

  toggleSelectedSocketCountList(state, ele) {
    var targetValue = ele.target.value;
    if (state.selectedSocketCountList.indexOf(targetValue) >= 0) {
      state.selectedSocketCountList.splice(
        state.selectedSocketCountList.indexOf(targetValue),
        1
      );
    } else {
      state.selectedSocketCountList.push(targetValue);
    }
  },

  clearSelectedSocketCountList(state) {
    state.selectedSocketCountList.splice(
      0,
      state.selectedSocketCountList.length
    );
  },
  changeSearchKeyword(state, ele) {
    state.searchKeyword = ele.target.value;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions: {},
  modules: {},
};
