import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";

const state = () => ({
  itemCategoryList: itemCategoryList,
});

const getters = {
  getItemCategoryByName: (state) => (name) => {
    for (var i = 0; i < state.itemCategoryList.length; i++) {
      if (state.itemCategoryList[i].name === name) {
        return state.itemCategoryList[i];
      }
    }
    return null;
  },
};

const mutations = {};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions: {},
  modules: {},
};
